@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  margin-top: 10px;
}

.calc-wrapper {
  width: 400px;
  height: 600px;
  margin-left: 420px;
}

.row {
  display: flex;
  width: 100%;
}

button {
  display: flex;
  height: 2.3em;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  background-color: #ff9ee6;
  color: black;
  outline: 1.33px solid black;
  font-family: 'Amatic SC', cursive;
  font-weight: bolder;
}

.clear {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  font-weight: lighter;
  font-size: 1.5rem;
  outline: 0px solid #888;
  height: 1em;
  background-color: #fff;
  font-family: 'Amatic SC', cursive;
  margin-top: 42px;
}

.operator {
  background-color: #e2a0ff;
  color: purple;
}

.result {
    display: flex;
    width: 366px;
    height: 1.8em;
    flex: 1 1;
    background-color: #fc52fc;
    outline: 2px solid #888;
    color: #fff;
    justify-content: flex-end;
    padding: 0.5em;
    font-weight: bold;
    font-size: 2em;
    align-items: center;
    font-family: 'Amatic SC', cursive;
    margin-left: 420px;
}

h1 {
    font-family: 'Amatic SC', cursive;
    font-size: 50px;
    margin-left: 550px;
    margin-top: 5px;
    margin-bottom: 5px;
}
